@keyframes strategiesMoveUp {
    to {
      transform: translateY(-530px);
      opacity: 0.1
    }
  }
  
  @keyframes mainAppear {
    from {
      opacity: 0;
    }
    50% {
      opacity: 0.4;
    }
    to {
      opacity: 1;
      z-index: 10;
    }
  }
  
  @keyframes heroHide {
    50% {
      opacity: 0.5;
    }
    to {
      z-index: 0;
      opacity: 0;
    }
  }
  
  @keyframes mainContentAppear {
    to {opacity: 1;
    }
  }